/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

#wrap {
  width: 100%;
  overflow-x: hidden; }

@include mobile {
  .container {
    padding-left: 30px;
    padding-right: 30px; } }

.btn {
  font-size: 10px;
  line-height: 20px;
  font-weight: 700;
  padding: 10px 24px;
  border-radius: 5px;
  border: 0;
  text-transform: uppercase;
  &-primary {
    background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    &:hover {
      background: transparent linear-gradient(180deg, #017961 0%, #017961 100%) 0% 0% no-repeat padding-box; }
    &:focus,
    &:active,
    &:active:focus {
      background: transparent linear-gradient(180deg, #017961 0%, #017961 100%) 0% 0% no-repeat padding-box;
      box-shadow: 0px 3px 6px #00000029 !important;
      outline: none; } }
  &-secondary {
    background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    &:hover {
      background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box; }
    &:focus,
    &:active,
    &:active:focus {
      background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box;
      box-shadow: 0px 3px 6px #00000029 !important;
      outline: none; } } }

.btn-detail {
  white-space: nowrap;
  img,
  .svg {
    position: relative;
    top: -1px;
    height: 16px;
    width: auto;
    display: inline-block;
    margin-left: 26px;
    path {
      transition: all .3s ease-in-out; } }
  &:hover {
    .svg path {
      fill: $primary; } } }

.badge {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  border-radius: 5px;
  padding: 8.5px 15px;
  min-width: 92px;
  &-primary {
    color: #FFFFFF;
    background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box; }
  &-secondary {
    color: #FFFFFF;
    background: transparent linear-gradient(178deg, #81C4FF 0%, #0D6AB7 100%) 0% 0% no-repeat padding-box; }
  &-warning {
    color: #FFFFFF;
    background: transparent linear-gradient(180deg, #FFDD80 0%, #FFBB00 100%) 0% 0% no-repeat padding-box; }
  &-block {
    width: 100%; } }


.accordion {
  .acc {
    position: relative;
    background: transparent;
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #707070;
    margin-bottom: 40px;
    .moreless {
      position: absolute;
      z-index: 2;
      right: 0;
      bottom: 12px;
      font-size: 12px;
      line-height: 1.5em;
      color: #70707090;
      text-transform: uppercase;
      cursor: pointer;
      transition: all .3s linear;
      &:hover {
        color: #707070; } }
    &-header {
      position: relative;
      background: transparent;
      border: 0; }
    &-title {
      display: block;
      position: relative;
      padding: 0 0 0 38px;
      margin: 0;
      transition: all .3s linear;
      cursor: pointer;
      color: $primary;
      font-size: 40px;
      line-height: 1.5em;
      letter-spacing: 0.42px;
      font-weight: 700;
      span.num {
        font-size: 15px;
        line-height: 1.5em;
        display: block;
        position: absolute;
        top: 12px;
        left: 0;
        width: 32px; }
      &.collapsed {
        padding-bottom: 40px;
        color: $text; } }
    &-body {
      padding: 12px 0 40px 40px;
      p {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.14px; } } }
  @include mobile {
    .acc {
      margin-bottom: 24px;
      .moreless {
        &.collapsed {
          bottom: 28px; } }
      &-title {
        font-size: 20px;
        line-height: 30px;
        padding: 0 0 0 20px;
        span.num {
          font-size: 10px;
          line-height: 1.5em;
          top: 4px; }
        &.collapsed {
          padding-bottom: 24px; } }
      &-body {
        padding: 12px 0 40px 20px; } } } }




.table {
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  td, th {
    padding: 20px 30px;
    border: 0;
    vertical-align: middle; }

  thead th {
    border-bottom: 1px solid #707070; } }



.table-striped {
  tbody {
    tr:nth-of-type(odd) {
      background: #FFFFFF; }
    tr:nth-of-type(even) {
      background: #F3F3F3; } } }


.loading-page {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #FFF;
  display: flex;
  align-items: center;
  text-align: center;
  // padding-bottom: 140px
  &__wrap {
    width: 100%; }
  &__logo {
    img {
      display: block;
      height: 106px;
      width: auto; }
    .icon {
      display: inline-block;
      img {
        @include logoRotation(); } }
    .text {
      display: inline-block;
      margin-left: 30px; } }
  .foot-text {
    display: block;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 70px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 700;
    text-align: center; }
  @include mobile {
    &__logo {
      img {
        height: 40px; }
      .text {
        margin-left: 8px; } }
    .foot-text {
      font-size: 10px;
      line-height: 18px;
      bottom: 80px; } } }


.btn-joinus {
  display: block;
  z-index: 10;
  position: fixed;
  right: -100%;
  bottom: 30px;
  color: #FFF;
  font-family: "Druk Wide", sans-serif;
  font-size: 10px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 45px 28px;
  background: {}
  background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
  border-radius: 5px 0 0 5px;
  cursor: pointer;
  transition: all 0.5s ease;
  transition-delay: 1s;
  @include mobile {
    display: none; } }

.mt-70 {
  margin-top: 70px;
  @include maxtablet {
    margin-top: 35px; } }
